    /// </summary>
    /// <param name="value">uint</param>
    public WriteUint(value:number)
    {
        this.bufView.setUint32(this.writeIndex,value,ByteBuffer.IslittleEndian);
        this.writeIndex+=4;
    }

    /// <summary>
    /// int64
