    /// uint16
    /// </summary>
    /// <returns>ushort</returns>
    public ReadUshort():number
    {
        let value=this.bufView.getUint16(this.readIndex,ByteBuffer.IslittleEndian);
        this.readIndex+=2;
        return value;
    }

